  font-size: 14px;
  line-height: 150%;
  text-align: center;
  color: #011c40;
  background-color: #d8d8d9;
  cursor: pointer;
}
@media screen and (max-width: 767px) {
  .cruises__toggle {
    min-width: 90%;
  }
}
@media screen and (max-width: 450px) {
  .cruises__toggle {
    min-width: 85%;
  }
}
@media screen and (max-width: 320px) {
  .cruises__toggle {
    min-width: 81%;
  }
}

.cruises__item:hover .cruises-container {
  display: block;
}

.cruises__item:hover .cruises__heading {
  margin: 30px 25px 20px;
  top: 0;
  font-size: 18px;
}
@media screen and (max-width: 1186px) {
  .cruises__item:hover .cruises__heading {
    font-size: 16px;
  }
}
@media screen and (max-width: 1023px) {
  .cruises__item:hover .cruises__heading {
    margin: 30px 35px;
    font-size: 18px;
  }
}
@media screen and (max-width: 767px) {
  .cruises__item:hover .cruises__heading {
    margin: 30px 25px;
  }
}

